// Generate realistic metadata
export function generateMetadata(city, device) {
  const dev = DEVICES[device] || DEVICES["iPhone 16 Pro"];
  const loc = CITIES[city] || CITIES["New York"];

  // Add realistic GPS variation (within ~2km)
  const lat = loc.lat + (Math.random() - 0.5) * 0.02;
  const lon = loc.lon + (Math.random() - 0.5) * 0.02;
  const alt = Math.max(0, loc.alt + Math.floor(Math.random() * 20) - 10);

  // Generate realistic timestamp (within last 7 days)
  const now = new Date();
  const daysAgo = Math.floor(Math.random() * 7);
  const date = new Date(now.getTime() - daysAgo * 24 * 60 * 60 * 1000);
  date.setHours(8 + Math.floor(Math.random() * 12));
  date.setMinutes(Math.floor(Math.random() * 60));
  date.setSeconds(Math.floor(Math.random() * 60));

  // Camera settings
  const isoValues = [32, 50, 64, 80, 100, 125, 160, 200, 250, 320, 400, 500, 640, 800];
  const shutterSpeeds = ['1/30', '1/60', '1/100', '1/125', '1/250', '1/500', '1/1000', '1/2000'];
  const iso = isoValues[Math.floor(Math.random() * isoValues.length)];
  const shutter = shutterSpeeds[Math.floor(Math.random() * shutterSpeeds.length)];

  // Unique content identifier
  const contentId = crypto.randomUUID().toUpperCase();

  // Format date strings
  const pad = (n) => String(n).padStart(2, '0');
  const dateStr = `${date.getFullYear()}:${pad(date.getMonth() + 1)}:${pad(date.getDate())} ${pad(date.getHours())}:${pad(date.getMinutes())}:${pad(date.getSeconds())}`;
  const dateWithTz = dateStr + loc.tz;

  return {
    make: dev.make,
    model: dev.model,
    software: dev.software,
    lensModel: dev.lensModel,
    focalLength: dev.focalLength,
    focalLength35mm: dev.focalLength35mm,
    aperture: dev.aperture,
    lat,
    lon,
    alt,
    latRef: lat >= 0 ? 'N' : 'S',
    lonRef: lon >= 0 ? 'E' : 'W',
    date: dateStr,
    dateWithTz,
    timezone: loc.tz,
    iso,
    shutter,
    exposureTime: parseShutter(shutter),
    contentId,
    gpsIso6709: formatGpsIso6709(lat, lon, alt),
    brightness: (4 + Math.random() * 4).toFixed(2),
    direction: Math.floor(Math.random() * 360),
    subsec: String(Math.floor(Math.random() * 1000)).padStart(3, '0'),
  };
}

function parseShutter(shutter) {
  // Parse shutter speed string like "1/30" to decimal
  const parts = shutter.split('/');
  if (parts.length === 2) {
    return parseFloat(parts[0]) / parseFloat(parts[1]);
  }
  return parseFloat(shutter);
}

function formatGpsIso6709(lat, lon, alt) {
  const latSign = lat >= 0 ? '+' : '-';
  const lonSign = lon >= 0 ? '+' : '-';
  const latStr = `${latSign}${Math.abs(lat).toFixed(4)}`;
  const lonStr = `${lonSign}${Math.abs(lon).toFixed(4).padStart(9, '0')}`;
  const altStr = `+${Math.floor(alt).toString().padStart(3, '0')}`;
  return `${latStr}${lonStr}${altStr}/`;
}

// ============================================
// PHOTO (JPEG) METADATA
// ============================================

export function injectPhotoMetadata(jpegData, metadata) {
  const data = new Uint8Array(jpegData);

  // Build EXIF segment
  const exif = buildExifSegment(metadata);

  // Remove existing EXIF
  const cleaned = removeExistingExif(data);

  // Insert new EXIF after SOI marker
  const result = new Uint8Array(cleaned.length + exif.length);
  result.set(cleaned.slice(0, 2)); // SOI (FFD8)
  result.set(exif, 2); // New EXIF
  result.set(cleaned.slice(2), 2 + exif.length); // Rest

  return result;
}

function removeExistingExif(data) {
  const result = [];
  let i = 0;

  result.push(data[0], data[1]); // SOI
  i = 2;

  while (i < data.length - 1) {
    if (data[i] === 0xFF && data[i + 1] === 0xE1) {
      // Skip APP1 (EXIF)
      const length = (data[i + 2] << 8) | data[i + 3];
      i += 2 + length;
      continue;
    }
    result.push(data[i]);
    i++;
  }

  while (i < data.length) result.push(data[i++]);
  return new Uint8Array(result);
}

function buildExifSegment(meta) {
  const buffer = [];

  // APP1 marker
  buffer.push(0xFF, 0xE1);
  const lengthPos = buffer.length;
  buffer.push(0x00, 0x00); // Length placeholder

  // EXIF header
  buffer.push(0x45, 0x78, 0x69, 0x66, 0x00, 0x00); // "Exif\0\0"

  // TIFF header (little endian)
  const tiffStart = buffer.length;
  buffer.push(0x49, 0x49); // Little endian "II"
  buffer.push(0x2A, 0x00); // TIFF magic
  buffer.push(0x08, 0x00, 0x00, 0x00); // IFD0 offset

  // IFD0 entries
  const ifd0Entries = [
    makeEntry(0x010F, 2, meta.make + '\0'),           // Make
    makeEntry(0x0110, 2, meta.model + '\0'),          // Model
    makeEntry(0x0112, 3, [1]),                         // Orientation
    makeEntry(0x0131, 2, meta.software + '\0'),       // Software
    makeEntry(0x0132, 2, meta.date + '\0'),           // DateTime
  ];

  // GPS entries
  const gpsEntries = [
    makeEntry(0x0001, 2, meta.latRef + '\0'),                           // GPSLatitudeRef
    makeEntry(0x0002, 5, gpsCoordToRational(Math.abs(meta.lat))),       // GPSLatitude
    makeEntry(0x0003, 2, meta.lonRef + '\0'),                           // GPSLongitudeRef
    makeEntry(0x0004, 5, gpsCoordToRational(Math.abs(meta.lon))),       // GPSLongitude
    makeEntry(0x0005, 1, [0]),                                          // GPSAltitudeRef
    makeEntry(0x0006, 5, [[Math.round(meta.alt * 100), 100]]),         // GPSAltitude
  ];

  // EXIF SubIFD entries
  const exifEntries = [
    makeEntry(0x829A, 5, [[Math.round(meta.exposureTime * 1000000), 1000000]]), // ExposureTime
    makeEntry(0x829D, 5, [[Math.round(meta.aperture * 100), 100]]),    // FNumber
    makeEntry(0x8827, 3, [meta.iso]),                                   // ISO
    makeEntry(0x9003, 2, meta.date + '\0'),                            // DateTimeOriginal
    makeEntry(0x9004, 2, meta.date + '\0'),                            // DateTimeDigitized
    makeEntry(0x920A, 5, [[Math.round(meta.focalLength * 100), 100]]), // FocalLength
    makeEntry(0xA405, 3, [meta.focalLength35mm]),                       // FocalLengthIn35mmFilm
    makeEntry(0xA433, 2, meta.make + '\0'),                            // LensMake
    makeEntry(0xA434, 2, meta.lensModel + '\0'),                       // LensModel
  ];

  // Calculate offsets
  const ifd0Size = 2 + ifd0Entries.length * 12 + 4;
  const exifIfdOffset = 8 + ifd0Size + calculateDataSize(ifd0Entries);
  const gpsIfdOffset = exifIfdOffset + 2 + exifEntries.length * 12 + 4 + calculateDataSize(exifEntries);

  // Add EXIF IFD pointer to IFD0
  ifd0Entries.push(makeEntry(0x8769, 4, [exifIfdOffset]));
  // Add GPS IFD pointer to IFD0
  ifd0Entries.push(makeEntry(0x8825, 4, [gpsIfdOffset]));

  // Write IFD0
  writeIFD(buffer, ifd0Entries, tiffStart, 8 + 2 + ifd0Entries.length * 12 + 4);

  // Write EXIF IFD
  buffer.length = tiffStart + exifIfdOffset;
  writeIFD(buffer, exifEntries, tiffStart, exifIfdOffset + 2 + exifEntries.length * 12 + 4);

  // Write GPS IFD
  buffer.length = tiffStart + gpsIfdOffset;
  writeIFD(buffer, gpsEntries, tiffStart, gpsIfdOffset + 2 + gpsEntries.length * 12 + 4);

  // Update length
  const length = buffer.length - 2;
  buffer[lengthPos] = (length >> 8) & 0xFF;
  buffer[lengthPos + 1] = length & 0xFF;

  return new Uint8Array(buffer);
}

function makeEntry(tag, type, value) {
  return { tag, type, value };
}

function gpsCoordToRational(coord) {
  const deg = Math.floor(coord);
  const minFloat = (coord - deg) * 60;
  const min = Math.floor(minFloat);
  const sec = Math.round((minFloat - min) * 60 * 10000);
  return [[deg, 1], [min, 1], [sec, 10000]];
}

function calculateDataSize(entries) {
  let size = 0;
  for (const e of entries) {
    const valueSize = getValueSize(e.type, e.value);
    if (valueSize > 4) size += valueSize;
  }
  return size;
}

function getValueSize(type, value) {
  if (type === 2) return value.length; // ASCII
  if (type === 3) return value.length * 2; // SHORT
  if (type === 4) return value.length * 4; // LONG
  if (type === 5) return value.length * 8; // RATIONAL
  if (type === 1) return value.length; // BYTE
  return 4;
}

function writeIFD(buffer, entries, tiffStart, dataOffset) {
  // Entry count
  buffer.push(entries.length & 0xFF, (entries.length >> 8) & 0xFF);

  let currentDataOffset = dataOffset;

  for (const entry of entries) {
    // Tag
    buffer.push(entry.tag & 0xFF, (entry.tag >> 8) & 0xFF);

    // Type
    buffer.push(entry.type & 0xFF, (entry.type >> 8) & 0xFF);

    // Count and value
    const { count, bytes } = encodeValue(entry.type, entry.value);
    buffer.push(count & 0xFF, (count >> 8) & 0xFF, (count >> 16) & 0xFF, (count >> 24) & 0xFF);

    if (bytes.length <= 4) {
      buffer.push(...bytes);
      while (buffer.length % 12 !== (tiffStart + 8 + 2) % 12 + 8) {
        if (bytes.length < 4) buffer.push(0);
        else break;
      }
      // Pad to 4 bytes
      while (bytes.length + buffer.length % 4 < 4) buffer.push(0);
      for (let i = bytes.length; i < 4; i++) buffer.push(0);
    } else {
      // Write offset
      buffer.push(
        currentDataOffset & 0xFF,
        (currentDataOffset >> 8) & 0xFF,
        (currentDataOffset >> 16) & 0xFF,
        (currentDataOffset >> 24) & 0xFF
      );
      currentDataOffset += bytes.length;
    }
  }

  // Next IFD (0)
  buffer.push(0, 0, 0, 0);

  // Write data for large values
  currentDataOffset = dataOffset;
  for (const entry of entries) {
    const { bytes } = encodeValue(entry.type, entry.value);
    if (bytes.length > 4) {
      while (buffer.length < tiffStart + currentDataOffset) buffer.push(0);
      buffer.push(...bytes);
      currentDataOffset += bytes.length;
    }
  }
}

function encodeValue(type, value) {
  const bytes = [];
  let count;

  if (type === 2) { // ASCII
    count = value.length;
    for (let i = 0; i < value.length; i++) {
      bytes.push(value.charCodeAt(i));
    }
  } else if (type === 3) { // SHORT
    count = value.length;
    for (const v of value) {
      bytes.push(v & 0xFF, (v >> 8) & 0xFF);
    }
  } else if (type === 4) { // LONG
    count = value.length;
    for (const v of value) {
      bytes.push(v & 0xFF, (v >> 8) & 0xFF, (v >> 16) & 0xFF, (v >> 24) & 0xFF);
    }
  } else if (type === 5) { // RATIONAL
    count = value.length;
    for (const [num, den] of value) {
      bytes.push(num & 0xFF, (num >> 8) & 0xFF, (num >> 16) & 0xFF, (num >> 24) & 0xFF);
      bytes.push(den & 0xFF, (den >> 8) & 0xFF, (den >> 16) & 0xFF, (den >> 24) & 0xFF);
    }
  } else if (type === 1) { // BYTE
    count = value.length;
    bytes.push(...value);
  } else {
    count = 1;
    bytes.push(0, 0, 0, 0);
  }

  return { count, bytes };
}

